}

.container {
  width: 100%;
  max-width: 480px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (width >= 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (width >= 1280px) {
  .container {
    max-width: 1280px;
  }
}

.header {
  background-color: #272e34;
  background-image: url("main-background-mobile@x3.60fb77c3.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  gap: 54px;
  padding-top: 40px;
  padding-bottom: 92px;
  display: flex;
}

@media screen and (width >= 768px) {
  .header {
    background-image: url("main-background-tablet@x3.30a814c8.jpg");
  }
}

@media screen and (width >= 1280px) {
  .header {
    background-image: url("main-background-desktop@x3.fed07bc0.jpg");
  }
}

.header div.container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-library {
