  padding-bottom: 62px;
}

@media screen and (width >= 768px) {
  .header-library {
    background-image: url("library-background-tablet@3x.24142859.jpg");
  }
}

@media screen and (width >= 1280px) {
  .header-library {
    background-image: url("library-background-desktop@3x.1cd3265e.jpg");
  }
}

.library {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.library__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #0000;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 14px 36px 12px;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

.library__button--current {
  background: #ff6b08;
  border: 1px solid #ff6b08;
  box-shadow: 0 8px 43px #ff6b0199;
}

.logo {
  fill: #fff;
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo p {
  color: #fff;
  text-align: center;
  font-family: Roboto;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: none;
}

@media screen and (width >= 768px) {
  .logo p {
    display: block;
  }
}

.logo:hover, .logo :focus {
  fill: #ff6b08;
}

.logo:hover p, .logo :focus p {
  color: #ff6b08;
}

.navigation__list {
  text-transform: uppercase;
  color: #fff;
  gap: 40px;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.navigation__list a {
  color: #fff;
}

.navigation__list a:hover, .navigation__list a :focus {
  color: #ff6b08;
}

.search-form {
  justify-content: center;
  display: flex;
  position: relative;
}

.search-form__label {
  border-bottom: 1px solid #fff;
  align-items: center;
  width: 100%;
  max-width: 280px;
  display: flex;
}

@media screen and (width >= 768px) {
  .search-form__label {
    max-width: 336px;
  }
}

.search-form__svg {
  fill: #fff;
  cursor: pointer;
}

.search-form__input {
  color: #fff;
  background-color: #0000;
  border: none;
  width: 100%;
  padding-bottom: 1px;
  padding-left: 1px;
}

.search-form__input::placeholder {
  color: #fff;
}

.search-form__input:focus, .search-form__input :active {
  outline: none;
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #ff6b08;
  width: 100%;
  height: 3px;
  display: inline-block;
  position: absolute;
  bottom: -2px;
  left: 0;
}

.is-empty {
  background-image: url("empty-library.b72c3c54.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.my-library {
  width: 100%;
  height: 100vw;
}

.main-section {
  flex-direction: column;
  gap: 20px;
  padding-top: 32px;
  padding-bottom: 32px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  line-height: 1.4;
  display: flex;
}

@media screen and (width >= 768px) {
  .main-section {
    flex-flow: wrap;
    justify-content: left;
    gap: 32px;
  }
}

@media screen and (width >= 1280px) {
  .main-section {
    gap: 16px;
  }
}

.film-poster {
  flex-direction: column;
  transition: transform .4s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media screen and (width >= 768px) {
  .film-poster {
    flex-basis: calc(50% - 16px);
  }
}

@media screen and (width >= 1280px) {
  .film-poster {
    flex-basis: calc(33.3333% - 10.6667px);
    padding-bottom: 16px;
  }

  .film-poster:hover {
    cursor: pointer;
    transform: scale(1.016);
  }
}

.film-poster__photo {
  border-radius: 5px;
  max-width: 100%;
  height: auto;
  max-height: 100%;
}

.film-poster__description {
  margin-top: 10px;
  font-size: 12px;
}

@media screen and (width >= 1280px) {
  .film-poster__description {
    font-size: 20px;
  }
}

.film-poster__description > p {
  display: inline;
}

.film-poster__title {
  text-transform: uppercase;
}

.film-poster__button {
  float: right;
  color: #fff;
  background-color: red;
  border: none;
  border-radius: 20%;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  margin-right: 3px;
  padding: 10px 28px;
  font-size: 22px;
  display: flex;
  position: relative;
  box-shadow: 3px 3px 3px #0009;
}

.film-poster__button:focus {
  outline: none;
}

.film-poster__genre, .film-poster__year {
  color: #ff6b08;
}

.pagination-section {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 1.4;
  display: none;
}

@media screen and (width >= 768px) {
  .pagination-section {
    justify-content: center;
    display: flex;
  }
}

.pagination-section > .current-page {
  color: #fff;
  background-color: #ff6b08;
}

.pagination-section > button {
  background-color: #fff;
  border: none;
  border-radius: 5px;
  width: 40px;
  height: 40px;
  padding-left: 2px;
  padding-right: 2px;
  font-size: large;
  font-weight: 500;
}

.pagination-section > button:first-child, .pagination-section > button:last-child {
  background-color: #f7f7f7;
  font-size: x-large;
}

.pagination-section > button:hover, .pagination-section > button:active {
  color: #fff;
  background-color: #ff6b08;
}

.pagination-section > button:hover > svg, .pagination-section > button:active > svg {
  fill: #fff;
}

.hidden {
  display: none;
}

.button--disabled:hover {
  background-color: #f7f7f7;
}

.modal-backdrop {
  z-index: 3;
  background-color: #0000004d;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition-property: opacity, background-color, visibility;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
}

.modal-trailer {
  position: absolute;
}

.is-hidden {
  display: none;
}

.modal-trailer__iframe {
  width: 500px;
  height: 300px;
}

.footer {
  color: #545454;
  text-align: center;
  opacity: .95;
  background-color: #f7f7f7;
  padding: 10px;
