}

@media screen and (width >= 768px) {
  .footer {
    opacity: 1;
    padding: 30px;
    font-size: 16px;
    position: relative;
    bottom: auto;
  }
}

.footer > p {
  text-align: center;
  align-items: center;
  margin: 0 auto;
  display: inline-block;
}

.footer__button {
  border: 0;
  border-bottom: 1px solid #545454;
  margin-left: 5px;
}

.heart-icon {
  fill: #ff6b08;
}

